
.engagement {
    width: 50%;
}

.form {
    text-align: center;
    width: 100%;
    width: -moz-available;
    width: -webkit-fill-available;
    width: fill-available;  
    place-self: center;
    height: 350px;
}

.formContainer {
    font-family: 'Avenir';
    display: flex;
    flex-direction: column;
    height: inherit;
}

.innerForm {
    margin: auto;
}

.error {
    color: red;
}

.rsvp-input {
    border: 0;
    outline: none;
    text-align-last: center;
}

.alignCenter {
    text-align: center;
}

.addlButton {
    font-size: 15px;
    border: 0;
}

.subButton {
    font-size: 20px;
    border: 0;
}
